<script>
import Vue from "vue";
import SortedTablePlugin from "vue-sorted-table";
import functions from "../../util/functions.js";
import constants from "../../util/constants";
import { mapState } from "vuex";

Vue.use(SortedTablePlugin, {
  ascIcon: '<i class="mdi mdi-arrow-down"></i>',
  descIcon: '<i class="mdi mdi-arrow-up"></i>',
});

export default {
  data: function () {
    return {
      constants,

      is_doing_action: false,
      action_message: "En cours",

      sortBy: "Nom & prénom",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      is_loading_articles: true,

      utilisateur : { id : -1},
      
      fields: [
        { key: "n", sortable: false },
        // { key: "action", sortable: true },
        { key: "nom_defunt", sortable: true },
        { key: "postnom_defunt", sortable: true },
        { key: "prenom_defunt", sortable: true },
        { key: "sexe_defunt", sortable: true },

        // { key: `lieu_naissance_defunt`, sortable: true },
        // { key: `date_naissance_defunt`, sortable: true },
        // { key: `proffesion_defunt`, sortable: true },
        // { key: `etat_civil_defunt`, sortable: true },
        // { key: `nom_conjoint(e)`, sortable: true },
        // { key: `date_deces`, sortable: true },
        // { key: `nom_pere`, sortable: true },
        // { key: `postnom_pere`, sortable: true },
        // { key: `prenom_pere`, sortable: true },
        // { key: `lieu_naissance_pere`, sortable: true },
        // { key: `date_naissance_pere`, sortable: true },
        // { key: `proffesion_pere`, sortable: true },
        // { key: `nationalite_pere`, sortable: true },
        // { key: `nom_mere`, sortable: true },
        // { key: `postnom_mere`, sortable: true },
        // { key: `prenom_mere`, sortable: true },
        // { key: `lieu_naissance_mere`, sortable: true },
        // { key: `date_naissance_mere`, sortable: true },
        // { key: `proffesion_mere`, sortable: true },
        // { key: `nationalite_mere`, sortable: true },
        // { key: `nom_utilisateur`, sortable: true }
      ],
      all_deces: [],
      list_categories_articles: [],
      search_text: "",
    };
  },
  async created() {
    this.is_loading_articles = true;
    try {
      this.utilisateur = JSON.parse(sessionStorage.getItem('utilisateur'));
    } catch(e) {
      this.utilisateur = {id : -1}
    }

    let url = this.constants.api_url + "/take/" + this.utilisateur.id;
    let response = await fetch(url, {
      method: "get",
      headers: {
        "Content-Type": "application/json"
      },
      // body: JSON.stringify({
      //   user_id: this.id_utilisateur,
      //   user_lat: -1,
      //   user_long: -1,
      // }),
      cache: "no-cache",
      //credentials: "include",
    });
    let data = await response.json();
    if (data.data) {  
      this.all_deces = data.data.map((deces) => {
        return {
          ...deces,
          n : data.data.findIndex(ff => ff.id == deces.id)+1, 
          action: deces.id,
        };
      });
    } else {
      this.$swal({
        type: "error",
        title: "Une erreur est survenue",
        text: "Status : " + data.status + "\nMessage : " + data.message,
        //footer: '<a href>Why do I have this issue?</a>'
      });
    }

    

    this.is_loading_articles = false;
  },
  computed: {
    ...mapState([
      "is_connected",
      "nom_utilisateur",
      "id_utilisateur",
      "est_admin",
    ]),
    rows() {
      return this.all_deces.length;
    },
    deces() {
      return this.all_deces.filter((article) => {
        if (this.search_text == "") {
          return this.all_deces;
        }
        let attrs = this.fields.map(ff => ff.key).filter(ff => ff!="n" && ff!="action");
        for (let attr of attrs) {
          if (
            article[attr] != undefined &&
            article[attr]
              .toLowerCase()
              .toLowerCase()
              .includes(this.search_text.toLowerCase())
          ) {
            return true;
          }
        }
      });
    },
  },
  methods: {
    ...functions,
    async doDeleteFaq(id_alerte) {
      let res = await this.$swal({
        title: "Supprimer ?",
        text: "Voulez-vous vraiment supprimer cet alerte ?",
        type: "question",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Supprimer",
        cancelButtonText: "Annuler",
      });
      if (!res.value) {
        return;
      }
      this.action_message = "Suppression  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/alert/" + id_alerte;
      let response = null;
      try {
        response = await fetch(url, {
          method: "delete",
          cache: "no-cache",
          body: JSON.stringify(this.article),
          credentials: "include",
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        this.all_deces = this.all_deces.filter((article) => article.id != id_alerte);
        this.$swal({
          type: "success",
          title: "Effectué",
          text: "La article a été supprimée avec succès. ",
          //footer: '<a href>Why do I have this issue?</a>'
        });
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
  },
};
</script>

<template>
  <section class="mt-5">
    <div class="page-header">
      <h3 class="page-title">Liste des decès</h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <a href="javascript:void(0);">Decès</a>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            Liste des decès
          </li>
        </ol>
      </nav>
    </div>
    <div v-if="!is_loading_articles">
      <div v-if="is_doing_action">
        <div class="card mb-4">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ action_message }}</p>
          </div>
        </div>
      </div>
      <div class=" ">
        <div class="card">
          <div class="card-body">
            <!-- <h4 class="card-title mb-5">Liste de Faqs</h4> -->
            <div class="row mb-4 d-flex justify-content-start">
              <div class="col-md-7 justify-content-start">
                <button type="button" @click="$router.push('/admin/deces/nouveau')" :disabled="is_doing_action"
                  class="btn-icon-text btn btn-primary mb-3 mb-md-0">
                  <i class="fa fa-plus btn-icon-prepend"></i>
                  Enregistrer un nouveau decès
                </button>
              </div>
              <div class="col-md-5 d-none">
                <b-form-group label="" label-for="input5">
                  <b-form-input type="text" v-model="search_text" placeholder="Rechercher"></b-form-input>
                </b-form-group>
              </div>
            </div>

            <div class="table-responsive">
              <b-table :items="deces" id="table-list" responsive :per-page="perPage" :current-page="currentPage"
                :fields="fields" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc">
                <template v-slot:cell(status)="data">
                  <span v-html="data.value"></span>
                </template>
                <template v-slot:cell(action)="data">
                  <div class="d-flex justify-content-center">
                    <button class="btn btn-sm d-none btn-outline-primary py-1 mr-1" title="Modifier ce article" @click="() => $router.push('/admin/deces/editer/' + data.value)
      " :disabled="is_doing_action">
                      <i class="fa fa-pencil"></i>
                    </button>
                    <button @click="doDeleteFaq(data.value)" class="btn btn-sm btn-outline-danger py-1"
                      title="Supprimer ce article" :disabled="is_doing_action">
                      <i class="fa fa-trash"></i>
                    </button>
                  </div>
                </template>
                <template v-slot:cell(profil)="data">
                  <span v-html="data.value"></span>
                </template>
              </b-table>
              <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" aria-controls="table-list"
                align="right">
              </b-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mt-5 pt-4" v-else>
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement des données en cours</p>
        </div>
      </div>
    </div>
    <div></div>
  </section>
</template>
